@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a2e 50%, #16213e 100%);
  color: white;
  overflow-x: hidden;
}

.animated-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: -1;
}

.floating-particles {
  position: absolute;
  width: 4px;
  height: 4px;
  background: linear-gradient(45deg, #00f5ff, #ff00ff);
  border-radius: 50%;
  animation: float 6s ease-in-out infinite;
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0px) rotate(0deg);
  }
  33% {
    transform: translateY(-20px) rotate(120deg);
  }
  66% {
    transform: translateY(10px) rotate(240deg);
  }
}

.glow-orb {
  position: absolute;
  border-radius: 50%;
  filter: blur(40px);
  animation: pulse 4s ease-in-out infinite;
}

@keyframes pulse {
  0%,
  100% {
    transform: scale(1);
    opacity: 0.3;
  }
  50% {
    transform: scale(1.2);
    opacity: 0.6;
  }
}

.navbar {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 50px;
  padding: 10px 30px;
  z-index: 1000;
  animation: slideDown 1s ease-out;
}

@keyframes slideDown {
  from {
    transform: translateX(-50%) translateY(-100px);
    opacity: 0;
  }
  to {
    transform: translateX(-50%) translateY(0);
    opacity: 1;
  }
}

.nav-links {
  display: flex;
  gap: 30px;
  list-style: none;
}

.nav-links a {
  color: white;
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
  position: relative;
}

.nav-links a:hover {
  color: #00f5ff;
}

.nav-links a::after {
  content: "";
  position: absolute;
  width: 0;
  height: 2px;
  bottom: -5px;
  left: 0;
  background: linear-gradient(90deg, #00f5ff, #ff00ff);
  transition: width 0.3s ease;
}

.nav-links a:hover::after {
  width: 100%;
}

.hero-section {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.hero-content {
  text-align: center;
  z-index: 10;
  animation: heroFadeIn 2s ease-out;
}

@keyframes heroFadeIn {
  from {
    transform: translateY(50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.hero-title {
  font-size: clamp(3rem, 8vw, 7rem);
  font-weight: 800;
  margin-bottom: 20px;
  background: linear-gradient(45deg, #00f5ff, #ff00ff, #00ff88);
  background-size: 300% 300%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: gradientShift 3s ease-in-out infinite;
}

@keyframes gradientShift {
  0%,
  100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

.hero-subtitle {
  font-size: clamp(1.2rem, 3vw, 2rem);
  margin-bottom: 30px;
  opacity: 0.9;
  animation: slideUp 1.5s ease-out 0.5s both;
}

@keyframes slideUp {
  from {
    transform: translateY(30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 0.9;
  }
}

.hero-description {
  font-size: 1.1rem;
  max-width: 600px;
  margin: 0 auto 20px;
  line-height: 1.6;
  opacity: 0.8;
  animation: slideUp 1.5s ease-out 1s both;
}

.cta-buttons {
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
  animation: slideUp 1.5s ease-out 1.5s both;
}

.btn {
  padding: 15px 30px;
  border: none;
  border-radius: 50px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.btn-primary {
  background: linear-gradient(45deg, #00f5ff, #0066ff);
  color: white;
}

.btn-secondary {
  background: transparent;
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
}

.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 20px 40px rgba(0, 245, 255, 0.3);
}

.section {
  padding: 100px 50px;
  max-width: 1200px;
  margin: 0 auto;
}

.section-title {
  font-size: clamp(2.5rem, 5vw, 4rem);
  text-align: center;
  margin-bottom: 60px;
  background: linear-gradient(45deg, #00f5ff, #ff00ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  margin-top: 60px;
}

.skill-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 30px;
  transition: all 0.3s ease;
  animation: cardSlideIn 1s ease-out;
}

@keyframes cardSlideIn {
  from {
    transform: translateY(50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.skill-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 30px 60px rgba(0, 245, 255, 0.2);
  border-color: rgba(0, 245, 255, 0.3);
}

.skill-icon {
  font-size: 3rem;
  margin-bottom: 20px;
  background: linear-gradient(45deg, #00f5ff, #ff00ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.experience-timeline {
  position: relative;
  margin-top: 60px;
}

.timeline-line {
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 2px;
  background: linear-gradient(180deg, #00f5ff, #ff00ff);
  transform: translateX(-50%);
}

.timeline-item {
  display: flex;
  margin-bottom: 60px;
  position: relative;
  animation: timelineSlide 1s ease-out;
}

@keyframes timelineSlide {
  from {
    transform: translateX(-50px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.timeline-content {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 30px;
  width: 45%;
  transition: all 0.3s ease;
}

.timeline-content:hover {
  transform: scale(1.02);
  box-shadow: 0 20px 40px rgba(0, 245, 255, 0.2);
}

.timeline-item:nth-child(even) {
  flex-direction: row-reverse;
}

.timeline-item:nth-child(even) .timeline-content {
  margin-left: auto;
}

.org-logos {
  display: flex;
  gap: 20px;
  align-items: center;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.org-logo {
  width: 50px;
  height: 50px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  animation: logoFloat 3s ease-in-out infinite;
}

@keyframes logoFloat {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-5px);
  }
}

.achievements-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  margin-top: 60px;
}

.achievement-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 30px;
  text-align: center;
  transition: all 0.3s ease;
  animation: achievementPop 1s ease-out;
}

@keyframes achievementPop {
  from {
    transform: scale(0.8);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.achievement-card:hover {
  transform: translateY(-10px) scale(1.05);
  box-shadow: 0 30px 60px rgba(255, 0, 255, 0.2);
}

.achievement-number {
  font-size: 3rem;
  font-weight: 800;
  background: linear-gradient(45deg, #00f5ff, #ff00ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 10px;
}

.contact-section {
  background: rgba(255, 255, 255, 0.02);
  backdrop-filter: blur(20px);
  border-radius: 30px;
  margin: 50px;
  padding: 80px 50px;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin-top: 40px;
  flex-wrap: wrap;
}

.social-link {
  width: 60px;
  height: 60px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: white;
  text-decoration: none;
  transition: all 0.3s ease;
  animation: socialBounce 2s ease-in-out infinite;
}

@keyframes socialBounce {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

.social-link:hover {
  background: linear-gradient(45deg, #00f5ff, #ff00ff);
  transform: scale(1.2) translateY(-5px);
  box-shadow: 0 20px 40px rgba(0, 245, 255, 0.4);
}

@media (max-width: 768px) {
  .section {
    padding: 50px 20px;
  }

  .nav-links {
    gap: 15px;
  }

  .nav-links a {
    font-size: 0.9rem;
  }

  .hero-title {
    font-size: 3rem;
  }

  .timeline-line {
    left: 20px;
  }

  .timeline-item {
    flex-direction: column !important;
    margin-left: 40px;
  }

  .timeline-content {
    width: 100% !important;
    margin-left: 0 !important;
  }

  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }

  .contact-section {
    margin: 20px;
    padding: 40px 20px;
  }
}

.tech-icons {
  display: flex;
  gap: 15px;
  margin: 20px 0;
  flex-wrap: wrap;
}

.tech-icon {
  padding: 8px 16px;
  background: rgba(0, 245, 255, 0.1);
  border: 1px solid rgba(0, 245, 255, 0.2);
  border-radius: 20px;
  font-size: 0.9rem;
  color: #00f5ff;
  animation: techGlow 2s ease-in-out infinite alternate;
}

@keyframes techGlow {
  from {
    box-shadow: 0 0 5px rgba(0, 245, 255, 0.2);
  }
  to {
    box-shadow: 0 0 20px rgba(0, 245, 255, 0.4);
  }
}

